<!--
 * @Description: 医生列表
 * @LastEditors: Ji Yinghao
 * @Date: 2019-04-30 12:52:55
 * @LastEditTime: 2019-10-09 18:18:42
 -->
<template>
  <div>
    <!-- 导航 -->
    <Breadcrumbs :items="items" />

    <Module1 ref="module1" />
    <!-- 数据表格 -->
    <v-card>
      <v-card-title>
        <v-layout row wrap>
          <v-flex xs6 sm4 md2>
            <v-text-field v-model="doctor_name" label="医生姓名"></v-text-field>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <v-text-field v-model="doctor_phone" label="手机号码"></v-text-field>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <v-select
              @change="info"
              menu-props="auto"
              v-model="doctor_type"
              :items="['全部医生', '未申请认证', '正在认证中', '认证被拒绝', '认证已通过']"
              label="医生状态"
              required
            ></v-select>
          </v-flex>
          <v-btn :loading="loader" :disabled="loader" color="primary" @click="info">筛选医生</v-btn>
          <v-spacer></v-spacer>

          <v-btn color="indigo darken-1" flat @click="doctors_fun(2)">
            医生执业点 - 列表
            <v-icon right dark>add_location</v-icon>
          </v-btn>

          <v-btn color="success darken-1" flat @click="coordinate_fun(2)">
            查找经纬度 - 地图
            <v-icon right dark>add_location</v-icon>
          </v-btn>
        </v-layout>
      </v-card-title>
      <!--
        headers : 表头
        items： 表格内容
        total-items ： 总共的数据数量 （手动设置项目的总数，就可以禁用内置排序和分页）
        disable-initial-sort 在初始渲染时禁用默认排序
        :hide-actions="true" 隐藏表格操作
        :dark="true" 暗主题
        :loading="true" 加载进度条
      -->
      <v-data-table
        :headers="headers"
        :items="desserts"
        class="elevation-1"
        :total-items="0"
        :rows-per-page-text="`每页行数`"
        :rows-per-page-items="pagingList"
        :disable-initial-sort="true"
        :loading="loader"
        @update:pagination="info"
      >
        <template v-slot:items="props">
          <tr @dblclick="doctor_info( props.item )">
          <td>
            <v-avatar size="36px">
              <img v-if="props.item.thumb_url" :src="props.item.thumb_url" alt="Avatar" />
              <v-icon v-else>tag_faces</v-icon>
            </v-avatar>
          </td>
          <td class="text-xs-centre">{{ props.item.name }}</td>
          <td class="text-xs-centre">
            <v-chip small>{{ (props.item.ave_score).toFixed(1) }}</v-chip>
          </td>
          <td class="text-xs-centre">{{ props.item.phone | cover }}</td>
          <td class="text-xs-centre">
            <span v-if="props.item.level == 1">主任医师</span>
            <span v-else-if="props.item.level == 2">副主任医师</span>
            <span v-else-if="props.item.level == 3">主治医师</span>
            <span v-else-if="props.item.level == 4">住院医师</span>
            <span v-else-if="props.item.level == 5">助理医师</span>
          </td>
          <td class="text-xs-centre">
            <div
              class="text-xs-center"
              v-for="(item_,index) in props.item.workplaces"
              :key="index"
              @click="location( props.item )"
            >
              <v-chip>{{ item_.area }} - {{ item_.name }} ( {{ item_.longitude }} , {{ item_.latitude }} )</v-chip>
            </div>
          </td>
          <td class="text-xs-centre">
            <v-chip
              v-if="props.item.pass_validation == 1"
              :color="`red lighten-4`"
              class="ml-0"
              label
              small
            >未申请认证</v-chip>
            <v-chip
              v-else-if="props.item.pass_validation == 3"
              :color="`red lighten-4`"
              class="ml-0"
              label
              small
            >认证被拒绝</v-chip>
            <v-chip
              v-else-if="props.item.pass_validation == 2"
              :color="`teal lighten-4`"
              class="ml-0"
              label
              small
            >认证中</v-chip>
            <v-chip
              v-else-if="props.item.pass_validation == 4"
              :color="`teal lighten-4`"
              class="ml-0"
              label
              small
            >认证已通过</v-chip>
          </td>
          <td class="text-xs-centre">{{ props.item.created_time | time }}</td>
          <td class="text-xs-centre">{{ (props.item.withdrawal).toFixed(2) }} 元</td>
          <td class="text-xs-centre">{{ (props.item.sum_draw).toFixed(2) }} 元</td>
          <td class="text-xs-centre">
            <v-menu
              bottom
              dark
              origin="center center"
              :close-delay="100"
              transition="slide-x-transition"
            >
              <template v-slot:activator="{ on }">
                <v-btn color="primary" dark v-on="on">操作选项</v-btn>
              </template>
              <v-list>
                <v-list-tile @click="doctor_info( props.item )">
                  <v-list-tile-title> <v-icon>assignment_ind</v-icon> 查看医生详情</v-list-tile-title>
                </v-list-tile>
                <template v-for="(item,index) in root_path">
                  <v-list-tile
                    :key="index+100"
                    v-if="item.url == '/api/v1/doctor/edit'"
                    @click="modifyMessage( props.item )"
                  >
                    <v-list-tile-title> <v-icon>create</v-icon> 编辑基本信息</v-list-tile-title>
                  </v-list-tile>
                </template>
                <template v-for="(item,index) in root_path">
                  <v-list-tile
                    :key="index+200"
                    v-if="item.url == '/api/v1/manages/messages'"
                    @click="location( props.item )"
                  >
                    <v-list-tile-title> <v-icon>room</v-icon> 编辑工作地点</v-list-tile-title>
                  </v-list-tile>
                </template>
                <!-- <template v-for="(item,index) in root_path">
                  <v-list-tile
                    :key="index+300"
                    v-if="item.url == '/api/v1/manages/messages'"
                    @click="chatList( props.item )"
                  >
                    <v-list-tile-title> <v-icon>vertical_split</v-icon> 查看聊天记录</v-list-tile-title>
                  </v-list-tile>
                </template> -->
                <template v-for="(item,index) in root_path">
                  <v-list-tile
                    :key="index+500"
                    v-if="item.url == '/api/v1/manages/messages'"
                    @click="doctor_Rating( props.item )"
                  >
                    <v-list-tile-title> <v-icon>star</v-icon> 评分/评价</v-list-tile-title>
                  </v-list-tile>
                </template>
                <template v-for="(item,index) in root_path">
                  <v-list-tile
                    :key="index+400"
                    v-if="item.url == '/api/v1/user/delete'"
                    @click="delete_box( props.item.doctor_id )"
                  >
                    <v-list-tile-title class="red--text"> <v-icon>person_add_disabled</v-icon> 删除医生</v-list-tile-title>
                  </v-list-tile>
                </template>
              </v-list>
            </v-menu>
          </td>
           </tr>
        </template>
        <!-- 当没有数据的时候，可以使用 no-data 插槽显示一段自定义HTML。 -->
        <template v-slot:no-data>
          <!-- <v-alert :value="true" color="warning" icon="priority_high" outline>没有更多数据了 :(</v-alert> -->
          <p class="p_text">没有更多数据了 :(</p>
        </template>
        <!-- 如果你还想添加一些额外的功能，比如每列过滤或者搜索，你可以使用 footer 插槽。 -->
        <!-- <template v-slot:footer>
          <td :colspan="headers.length">
            <strong>总计：</strong>
          </td>
        </template>-->
        <!-- 你可以使用page-text 插槽来自定义显示范围以及总条目。 -->
        <template
          v-slot:pageText="props"
        >当前 {{ props.pageStart }} - {{ props.pageStop }} 总共 {{ props.itemsLength }}</template>
      </v-data-table>
    </v-card>

    <!-- 确认框 -->
    <v-dialog v-model="dialog_logout" max-width="290">
      <v-card>
        <v-card-title class="headline">删除操作</v-card-title>
        <v-card-text>确认删除该医生吗？确认删除将不可恢复</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat="flat" @click="dialog_logout = false">取消</v-btn>
          <v-btn color="green darken-1" flat="flat" @click="doctor_delete">确认删除</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- 编辑工作地点 -->
    <v-layout row justify-center>
      <v-dialog v-model="dialog" persistent max-width="1100px">
        <v-card>
          <v-card-title>
            <span class="headline">医生工作地点</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap v-for="(item,index) in workplaces" :key="index">
                <v-flex xs12 sm6 md2>
                  <!-- <v-text-field label="省份" hint="请输入工作所在省份" v-model="item.A"></v-text-field> -->
                  <v-overflow-btn
                    :items="item.AL"
                    v-model="item.A"
                    label="选择省份"
                    hide-details
                    class="pa-0"
                    style="margin-top: 0px;"
                    @input="one__(item.A,index)"
                  ></v-overflow-btn>
                </v-flex>
                <!-- <v-divider vertical></v-divider> -->
                <v-flex xs12 sm6 md2>
                  <!-- <v-text-field label="城市" hint="请输入工作所在城市" v-model="item.B"></v-text-field> -->
                  <v-overflow-btn
                    :items="item.BL[item.A]"
                    v-model="item.B"
                    editable
                    label="选择城市"
                    hide-details
                    class="pa-0"
                    overflow
                    style="margin-top: 0px;"
                  ></v-overflow-btn>
                </v-flex>
                <!-- <v-divider class="mr-2" vertical></v-divider> -->

                <!-- <v-flex xs12 sm6 md3>
                  <v-text-field label="省市" hint="请输入工作所在省市" v-model="item.area"></v-text-field>
                </v-flex>-->
                <v-flex xs12 sm12 md4>
                  <v-text-field label="医院名称" hint="请输入医院全称" v-model="item.name"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md2>
                  <v-text-field label="地理经度" hint="请输入医院所在经度" v-model="item.longitude"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md2>
                  <v-text-field label="地理纬度" hint="请输入医院所在纬度" v-model="item.latitude"></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
            <small>* 为了计算精度，请至少保留4-6位小数经度。</small>
          </v-card-text>
          <v-card-actions>
            <v-btn color="success darken-1" flat @click="coordinate_fun(2)">
              查找经纬度 - 高德地图
              <v-icon right dark>add_location</v-icon>
            </v-btn>
            <v-btn color="success darken-1" flat @click="coordinate_fun(1)">
              查找经纬度 - 百度地图
              <v-icon right dark>add_location</v-icon>
            </v-btn>
            <v-spacer></v-spacer>
          </v-card-actions>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="warning darken-1" @click="cancel()">取消</v-btn>
            <v-btn color="info darken-1" @click="location_()">提交</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
    <!-- 高德地图 -->
    <v-layout row justify-center>
      <v-dialog v-model="dialog_tu" fullscreen hide-overlay transition="dialog-bottom-transition">
        <v-card>
          <v-toolbar dark color="primary">
            <v-btn icon dark @click="dialog_tu = false">
              <v-icon>close</v-icon>
            </v-btn>
            <v-toolbar-title>搜索医院以获取坐标</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items>
              <v-btn dark flat @click="dialog_tu = false">关闭</v-btn>
            </v-toolbar-items>
          </v-toolbar>
          <v-divider></v-divider>
          <div id="tu">
            <iframe :src="coor" frameborder="0"></iframe>
          </div>
        </v-card>
      </v-dialog>
    </v-layout>
    <!-- 医生执业点 -->
    <Module2 ref="module2" ></Module2>
  </div>
</template>
<script>
import Breadcrumbs from "../../../components/breadcrumbs/breadcrumbs.vue";
import Module1 from "./components/module1.vue";
import Module2 from "./components/module2.vue";
import Location from "../../../server/location.js";
export default {
  components: {
    Breadcrumbs,
    Module1,
    Module2
  },
  data() {
    return {
      dialog: false,
      doctor_name: "",
      doctor_phone: "",
      // 确认框
      dialog_logout: false,
      doctor_type: "",
      pagingList: [10, 20, 30, 40, 50, { text: "100", value: 100 }],
      loader: false,
      page: 0,
      items: [
        { text: "首页", disabled: false, to: "/home" },
        { text: "医生列表", disabled: false, to: "/doctorList" }
      ],
      headers: [
        { text: "头像", align: "left", sortable: false, value: "name" },
        { text: "医生姓名", value: "calories", sortable: false },
        { text: "评分", value: "calories", sortable: false },
        { text: "手机号码", value: "fat", sortable: false },
        { text: "医生的职称", value: "carbs", sortable: false },
        { text: "工作地点", value: "protein", sortable: false },
        { text: "是否通过认证", value: "iron", sortable: false },
        { text: "创建时间", value: "iron", sortable: false },
        { text: "账户可提现", value: "iron", sortable: false },
        { text: "已提现金额", value: "iron", sortable: false },
        { text: "操作", value: "iron", sortable: false }
      ],
      desserts: [],
      root_path: this.$cookie.getLocalStorage("root_path").root_path,
      workplaces: [],
      dialog_tu: false,
      doctor_id: 0,
      // 查经纬度
      coordinate: [
        "http://api.map.baidu.com/lbsapi/getpoint/index.html",
        "https://lbs.amap.com/console/show/picker"
      ],
      coor: ""
    };
  },
  methods: {
    // 评分评价
    doctor_Rating(data){
      this.$router.push({
        path: '/doctorRating',
          query: {
            id: data.doctor_id,
            name: data.name,
            img: data.thumb_url
          }
      });
    },

    one__(name,index){
      if(index>=0){
        this.workplaces[ index ].B = this.workplaces[ index ].BL[ name ][0];
      }
    },
    // 打开地图
    coordinate_fun(num) {
        this.$Dialog.getProductCheckResult_({text: "输入医院名称以获取地理坐标（如果页面加载失败，请关闭重试）",timeout: 3000, snackbar: true });
      num == 1
        ? (this.coor = this.coordinate[0])
        : num == 2
        ? (this.coor = this.coordinate[1])
        : (this.coor = this.coordinate[0]);
      this.dialog_tu = true;
    },
    // 打开医生执业点
    doctors_fun(){
      this.$refs.module2.dialogShow(true);
    },
    // 编辑位置取消按钮
    cancel() {
      this.dialog = false;
      this.info();
    },
    // 编辑位置弹窗
    location(data) {
      this.dialog = true;
      this.doctor_id = data.doctor_id;
      this.workplaces = data.workplaces;
    },
    // 编辑位置提交按钮
    location_() {
      this.workplaces.forEach(element => {
        element.area = `${element.A},${element.B}`;
        element.latitude = element.latitude - 0;
        element.longitude = element.longitude - 0;
      });

      this.$axios({
        method: "post",
        url: this.$apis.URL.doctor_location,
        data: {
          doctor_id: this.doctor_id,
          workplaces: this.workplaces
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.doctor_id = 0;
            this.info();
            this.dialog = false;
            this.$Dialog.getProductCheckResult_({text: "位置信息修改成功！",timeout: 3000, snackbar: true });
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    },
    doctor_info(data) {
      this.$router.push({
        path: `/doctorInfo`,
        query: {
          data: data
        }
      });
    },

    loader_() {
      this.loader = true;
      setTimeout(() => (this.loader = false), 500);
    },
    // 修改操作
    modifyMessage(list) {
      let datas = {
        state: true,
        list: list
      };
      this.$refs.module1.info(datas);
    },
    // 删除确认框
    delete_box(id) {
      this.doctor_id = id;
      this.dialog_logout = true;
    },
    // 删除操作
    doctor_delete() {
      // 删除医生操作
      this.$axios({
        method: "post",
        url: this.$apis.URL.doctor_delete,
        data: {
          doctor_id: this.doctor_id
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.$Dialog.getProductCheckResult_({text: "医生已删除，操作完成",timeout: 3000, snackbar: true });
            this.dialog_logout = false;
            this.info();
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    },
    info(event) {
      // console.log(Object.keys(Location.location));
      this.loader = true;
      // ['全部医生', '未申请认证', '正在认证中', '认证被拒绝', '认证已通过']
      let status = 0;
      switch (this.doctor_type) {
        case "全部医生":
          status = 0;
          break;
        case "未申请认证":
          status = 1;
          break;
        case "正在认证中":
          status = 2;
          break;
        case "认证被拒绝":
          status = 3;
          break;
        case "认证已通过":
          status = 4;
          break;
        default:
          status = 0;
          break;
      }
      this.$axios({
        method: "post",
        url: this.$apis.URL.doctor_list,
        data: {
          status: status,
          user_name: this.doctor_name,
          phone: this.doctor_phone,
          page: this.page,
          page_count:
            typeof event == "undefined" ? undefined : event.rowsPerPage
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            // 添加双向绑定的对应城市
            if (response.data.datas.list) {
              response.data.datas.list.forEach(item => {
                item.workplaces.forEach(element => {
                  if (element.area.indexOf(",") != -1) {
                    element.A = element.area.split(",")[0];
                    element.B = element.area.split(",")[1];
                    element.AL = Object.keys(Location.location);
                    element.BL = Location.location;
                  } else {
                    element.A = element.area;
                    element.B = element.area;
                    element.AL = Object.keys(Location.location);
                    element.BL = Location.location;
                  }
                });
              });
            }
            this.desserts = response.data.datas.list;
            this.allList = response.data.datas.total_number;
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
          this.loader = false;
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    },
    // 聊天记录列表
    // chatList(data) {
    //   this.$router.push({
    //     path: `/chatList`,
    //     query: {
    //       user_data: data
    //     }
    //   });
    // }
  },
  watch: {}
};
</script>

<style lang="less" scoped>
.flex {
  padding-right: 10px;
  padding-left: 10px;
}
img {
  border: 1px #555 dashed;
}
#tu {
  height: calc(100vh - 80px);
  display: inline-block;
  width: 100%;
  padding-left: 10px;
  padding-top: 10px;
  iframe {
    height: 100%;
    width: calc(100vw - 20px);
  }
}
</style>
