 * @Description: 医生执业点
 * @LastEditors: Ji Yinghao
 * @Date: 2019-05-05 13:42:39
 * @LastEditTime: 2019-09-27 16:20:54
 -->
<template>
  <div>
    <!-- 医生执业点 -->
    <v-layout row justify-center>
      <v-dialog v-model="doctors_tu" fullscreen hide-overlay transition="dialog-bottom-transition">
        <v-card>
          <v-toolbar dark color="primary">
            <v-btn icon dark @click="doctors_tu = false">
              <v-icon>close</v-icon>
            </v-btn>
            <v-toolbar-title>已收录的医生执业点</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items>
              <v-btn dark flat @click="doctors_tu = false">关闭</v-btn>
            </v-toolbar-items>
          </v-toolbar>
          <v-divider></v-divider>
          <div id="tu">

            <v-card class="mx-auto" >
              <v-sheet class="pa-3 primary lighten-2">
                <v-text-field
                  v-model="search"
                  label="搜索医院"
                  dark
                  flat
                  solo-inverted
                  hide-details
                  clearable
                  clear-icon="mdi-close-circle-outline"
                ></v-text-field>
                <v-checkbox v-model="caseSensitive" dark hide-details label="模糊搜索"></v-checkbox>
                <v-btn color="success" @click="places_key" > + 添加医生执业点 </v-btn>
              </v-sheet>
              <v-card-text>
                <v-treeview :items="items" :search="search" :filter="filter" :open.sync="open" >
                  <template v-slot:prepend="{ item }">
                    <v-icon
                      v-if="item.children"
                      v-text="`mdi-${item.id === 1 ? 'home-variant' : 'folder-network'}`"
                    ></v-icon>
                  </template>
                </v-treeview>
              </v-card-text>
            </v-card>

            <div class="text-xs-left">
              <template v-for="(item, index) in items"  >
                <div v-for="(item1, index1) in item.children" :key="index1+index*10" class="list___" >
                  <p> {{ item.name }} / {{ item1.name }}</p> 
                  <v-chip color="grey" text-color="white" v-for="(item2, index2) in item1.children" :key="index2*100" >{{ item2.name }}</v-chip>
                </div>
              </template>
            </div>

          </div>
          <p v-if="items.length == 0" class="nan_" > 暂无数据... </p>
        </v-card>
      </v-dialog>
    </v-layout>
    <!-- 添加医生执业点 -->
    <v-layout row justify-center>
      <v-dialog v-model="dialog" persistent max-width="1000px">
        <v-card>
          <v-card-title>
            <span class="headline">添加医生执业点</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-container grid-list-md>
              <v-layout wrap >
                <v-flex xs12 sm6 md6>
                  <v-overflow-btn
                    :items="Object.keys(workplaces)"
                    v-model="hospital.province"
                    label="选择省份"
                    hide-details
                    class="pa-0"
                    style="margin-top: 0px;"
                    @input="one__(hospital.province)"
                  ></v-overflow-btn>
                </v-flex>
                <v-flex xs12 sm6 md6>
                  <v-overflow-btn
                    :items="workplaces[hospital.province]"
                    v-model="hospital.city"
                    editable
                    label="选择城市"
                    hide-details
                    class="pa-0"
                    overflow
                    style="margin-top: 0px;"
                  ></v-overflow-btn>
                </v-flex>
                <v-flex xs12 sm12 md12>
                  <v-text-field label="医院名称" hint="请输入医院全称" v-model="hospital.hosp[0].name" ></v-text-field>
                </v-flex>
                <v-flex xs12 sm12 md12>
                  <v-text-field label="医院位置描述" hint="XX街道XX路XXX号" v-model="hospital.hosp[0].address" ></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md12>
                  <v-text-field label="地理经度" hint="请输入医院所在经度" disabled ></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md12>
                  <v-text-field label="地理纬度" hint="请输入医院所在纬度" disabled ></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
                
              </v-layout>
            </v-container>
            <!-- <small>*敏感的权限请谨慎添加</small> -->
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="dialog = false">取消</v-btn>
            <v-btn color="info darken-1" @click="upData">提交</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>

  </div>
</template>

<script>
import Location from "../../../../server/location.js";
export default {
  data() {
    return {
      // 医生执业点
      doctors_tu: false,
      // 添加执业点框
      dialog: false,
      workplaces: Location.location,
      hospital:{
            "province": "广东省",
            "city": "深圳市",
            "hosp": [
                {
                    "name": "",
                    "address": ""
                }
            ]
        },
      // 列表数据
      items: [],
      open: [1, 10],
      search: null,
      caseSensitive: true
    };
  },
 computed: {
      filter () {
        return this.caseSensitive
          ? (item, search, textKey) => item[textKey].indexOf(search) > -1
          : undefined
      }
    },
  mounted() {
     this.info();
  },
  methods: {
    // 父组件触发
    dialogShow(data) {
      this.doctors_tu = data;
    },
    // 提交
    upData(){
      this.dialog = false;
      this.$axios({
        method: "post",
        url: this.$apis.URL.doctor_upload_hosp,
        data: this.hospital
      })
        .then(response => {
          if (response.data.result === 0) {
            this.info();
            this.$Dialog.getProductCheckResult_({text: "新的医生执业点医院 添加成功！",timeout: 3000, snackbar: true });
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
            this.dialog = true;
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
          this.dialog = true;
        });
    },
    // 添加医生执业点弹框
    places_key(){
      this.dialog = true;
    },
    // 城市选择器联动
    one__(name){
      this.hospital.city = this.workplaces[ name ][0]
    },
    // 所有医生执业点列表
    info(){
      // doctor_hosp_list
      this.$axios({
        method: "post",
        url: this.$apis.URL.doctor_hosp_list,
        data: {}
      })
        .then(response => {
          if (response.data.result === 0) {
            let list = [];
            response.data.datas.forEach((element,index1) => {
              list.push({
                id: index1+1,
                name: element.province,
                children: []
              }
              )
              element.cities.forEach((item,index2) => {
                list[index1].children.push({
                  id: (index1+1)*10+index2,
                  name: item.city,
                  children: []
                })
                item.hosp.forEach((hosp,index3) => {
                  list[index1].children[index2].children.push({
                    id: (index1+1)*100+index2*10+index3,
                    name: hosp.name + "(" + hosp.address + ")",
                    children: []
                  })
                })
              })
            });
            this.items = list;
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    }
  }
};
</script>

<style scoped>
  .list___{
    padding: 10px 15px;
  }
  .list___>p{
    margin-bottom: 5px;
  }
  .nan_{
    text-align: center;
    font-size: 18px;
    color: #565656;
    padding-top: 40px;
  }
</style>