<template>
  <div id="switch_">
    <!-- <v-btn color="primary" @click="dialog = true" >+ 创建子账号123 </v-btn> -->
    <v-layout row justify-center>
      <v-dialog v-model="dialog" persistent max-width="800px">
        <!-- <template v-slot:activator="{ on }">
          <v-btn color="primary" flat dark v-on="on">+ 创建子账号 {{ dialog }} </v-btn>
        </template>-->

        <v-card>
          <v-card-title>
            <span class="headline">修改医生信息</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12>
                  <v-text-field v-model="phone" label="医生手机号码" type="text" maxlength="11" required></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field v-model="doctor_name" label="医生姓名" type="text"  required></v-text-field>
                </v-flex>

                <!-- <v-flex xs12>
                  <v-select v-model="level" :items="['管理人员', '代理人员']" label="管理/代理角色" required></v-select>
                </v-flex>
                <v-flex v-for="(item,index) in root_path" :key="index" xs3>
                  <v-switch color="red darken-3" v-model="item.state" :label="item.name"></v-switch>
                </v-flex> -->
              </v-layout>
            </v-container>
            <!-- <small>*敏感的权限请谨慎添加</small> -->
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="dialog = false">取消</v-btn>
            <v-btn color="info darken-1" @click="childMethod($event)">提交修改</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialog: false,
      doctor_id:"",
      doctor_name: "",
      phone: ""
    };
  },
  // 更新完成
  mounted: function() {},
  methods: {
    info(data) {
          this.doctor_id = data.list.doctor_id
          this.doctor_name = data.list.name
          this.phone = data.list.phone
          this.dialog = true;
    },
    childMethod(event) {
      if (this.doctor_name.length < 2 || this.doctor_name.length > 16) {
        this.$Dialog.getProductCheckResult_({text: "请输入正确格式的用户姓名!",timeout: 3000, snackbar: true });
        return false;
      }
      if (this.phone.length !== 11) {
        this.$Dialog.getProductCheckResult_({text: "请输入正确的手机号码!",timeout: 3000, snackbar: true });
        return false;
      }
     
      this.$axios({
        method: "post",
        url: this.$apis.URL.doctor_edit,
        data: {
          doctor_id: this.doctor_id,
          doctor_name: this.doctor_name,
          phone: this.phone
        }
      })
        .then(response => {
          //请求成功返回的数据

          if (response.data.result === 0) {
            this.$Dialog.getProductCheckResult_({text: "修改医生成功!",timeout: 3000, snackbar: true });
            this.doctor_name = ""
            this.phone = ""
            this.dialog = false;
            // 刷新列表
            this.$parent.info();
          } else {
            // 服务器异常
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    }
  }
};
</script>

<style lang="less">
// #switch_{
.v-input__slot {
  margin-bottom: 0px !important;
}
.v-input--selection-controls {
  margin-top: 0px !important;
}
// }
</style>
